/* Creemos una funci√≥n vac√≠a */
function AutoPlay(){}

/* Le agregamos al objeto Autoplay la funci√≥n run a su prototype */
AutoPlay.prototype.run = function(player){
    if (!player.muted){
        player.muted = true;
    }
    player.play();
}

/* Declaramos que vamos a exportar la funci√≥n AutoPlay */
export default AutoPlay;